.box{
    margin-left: 10px;
}

.Button {
    display: flex;
    border: 0;
    background-color: var(--primary-background-color);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
  }
  
  .Container {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;
  }
  
  .Image {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    border-radius: 5px;
  }
  
  .Title,
  .Subitle {
    margin-bottom: 16px;
  }
  
  .Overview {
    color: var(--secondary-text-color);
    font-weight: bold;
  }
  
  .Description {
    color: var(--secondary-text-color);
    font-weight: bold;
  }
  
  .Link {
    color: var(--secondary-text-color);
    font-weight: bold;
    cursor: pointer;
    transition: color 250ms ease-in-out;
  }
  
  .Link:hover {
    color: var(--accent-color);
  }